import React, { Suspense, useMemo } from "react";
import {
  OrbitControls,
  Loader,
  PerspectiveCamera,
  Stars,
} from "@react-three/drei";
import { useSpring, animated } from "@react-spring/three";
import { Canvas } from "@react-three/fiber";
import { space, returnArray } from "../utils/";
import {
  EffectComposer,
  DepthOfField,
  Bloom,
  Noise,
  Vignette,
} from "@react-three/postprocessing";
import { SimpleShader } from "../shaders/HeartfeltShader";
import BackgroundSun from "../three/BackgroundSun";
import TextureOptions from "../hooks/TextureOptioins";
import GeoPlaneShader from "../three/GeoPlaneShader";
import useResize from "../hooks/useResize";
export default function CanvasBackground() {
  const { width, height } = useResize();
  return (
    <Suspense fallback={<Loader />}>
      <Canvas id='background-image' performance={{ min: 0.5 }} gl flat linear shadows>
        <BackgroundSun />
        <ambientLight color="white" intensity={0.25} />
        <EffectComposer>
          <DepthOfField
            focusDistance={7}
            focalLength={9}
            bokehScale={100}
            height={180}
          />
          <Bloom luminanceThreshold={400} luminanceSmoothing={90} height={30} />
          <Noise opacity={0.05} />
          <Vignette eskil={true} offset={0.05} darkness={6.1} />
        </EffectComposer>
      </Canvas>
    </Suspense>
  );
}
